"use client";
import React, { useState } from "react";
import { Job, Applicant } from "./types";

const initialJobs: Job[] = [
  { id: 1, title: "Frontend Developer", description: "React & Next.js", applicants: [] },
  { id: 2, title: "Backend Developer", description: "Node & Express", applicants: [] },
];

export default function JobsList() {
  const [jobs, setJobs] = useState<Job[]>(initialJobs);

  return (
    <div className="bg-white rounded-lg shadow p-6">
      <h2 className="text-xl font-semibold mb-4">Jobs List</h2>
      <table className="w-full text-left border-collapse">
        <thead>
          <tr>
            <th className="border-b p-2">Title</th>
            <th className="border-b p-2">Description</th>
            <th className="border-b p-2">Applicants</th>
          </tr>
        </thead>
        <tbody>
          {jobs.map(job => (
            <tr key={job.id} className="hover:bg-gray-50">
              <td className="p-2">{job.title}</td>
              <td className="p-2">{job.description}</td>
              <td className="p-2">{job.applicants.length}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
